<template> 
  <div id="HomeNotLogin"> 
        <div class="container">
            <div class="site"> {{site}}</div>
            <p> {{name}}</p>
            <hr  />
            <div class="br10"></div>
           <button type="button" class="btn btn-outline-secondary btn-sm">现在注册</button>
           <div class="br10" />
            <div  >已注册用户 <a href="#" >登录</a></div>
            <div class="br10" />
        </div> 
  </div>
</template>

<script>
export default {
  name: "HomeNotLogin",
  data() {
    return {
      site: "ITGeek.Top",
      name: "极客",
      datas: ["编程技术", "谈说", "互联网", "娱乐"]
    };
  }
};
</script>

<style>
#HomeNotLogin { 
  padding-top: 10px;
  margin-bottom: 5px;
  background-color: white;
}
.site{
    font-size: 18px;
    color: #515a6e;
} 
.br10{
    height: 10px;
}
</style>



