<template> 
  <div id="RecommendThremeLayout">
    <div class="title text-left">相关内容</div>
    <hr />
    <ol class="list-group text-left">
        <li v-for="item in datas"><router-link  :to="{path:'/ThremeDetailLayout',query:{title:item}}">    {{item}}</router-link> </li> 
    </ol>
  </div>
</template>

<script>
export default {
  name: 'RecommendThremeLayout',
  data () {
    return {
      datas: [
          '微信公众号文章采集,并发布到WordPress',
          'Java并发编程设计原则与模式',
           '面向对象编程设计模式--简单工厂模式讲解(历史上最简单明白的例子)',
           '面向对象编程设计模式--简单工厂模式讲解(历史上最简单明白的例子)',
           '面向对象编程设计模式--简单工厂模式讲解(历史上最简单明白的例子)', 
           '从头认识java-7.6 适配设计模式究竟适配类？还是适配接口？'
      ]
    }
  }
}
</script>

<style>
 #RecommendThremeLayout{
     margin-top: 10px;
     background-color: white;
     padding: 5px;
 } 
 .title{
     line-height: 40px;
     font-size: 17px;
 }
a{
    font-style: initial; 
    color: black;
}
 ol li{
    margin-left: 20px;
    line-height: 30px;
    font-size: 14px;
    border-bottom: 1px dashed #e2e2e2;
 }
</style>



