<template> 
  <div id="navigation">
    <nav class="navbar navbar-default navbar-fixed-top">
        <div class="container">
        <ul class="nav ">
              <li   class="nav-item "><router-link class="nav-link" to="/"> <img src="@/assets/logo.png" width="24" height="24" /> {{siteName}}</router-link></li>
              <li   class="nav-item"  v-for="item in datas" ><router-link class="nav-link" :to="item.url">{{item.name}}</router-link></li> 
        </ul>
       
          <ul class="nav navbar-right"> 
              <li class="nav-item justify-center">  
                 <form class="form-inline ">
                  <input class="form-control form-control-sm" type="search" placeholder="Search" aria-label="Search"> 
                </form>
            </li> 
              <li   class="nav-item"><a class="nav-link " href="#">  注册</a></li>
              <li   class="nav-item"><a class="nav-link " href="#"> 登录</a></li>
        </ul>
        </div>
    </nav>
  </div>
</template>

<script>
export default {
  name: 'Navigation',
  created: function(){
     
  },
  data () {
    return {
      siteName:"泡在网上的日子",
      datas: [   ]
      // datas: ['首页','话题','代码','导航','问答','关于']
    }
  },
  mounted: function(){
      this.$http.get('http://localhost:10100/blog/v1/nav?token=1').then(function(res){
        this.datas = res.data;
        console.log(datas); 
			},function(res){ console.log(res.status) });
  }
}
</script>

<style>
 #navigation{
   background-color: white;
 }
 input[type=search]{outline: none;}
 hr{margin: 0px;}
</style>



