import Vue from 'vue'
import Router from 'vue-router'
import HomeLayout from '@/page/HomeLayout'
import ThremeDetailLayout from '@/page/ThremeDetailLayout' 
Vue.use(Router)
export default new Router({
  routes: [
    { path: '/', name: 'HomeLayout', component: HomeLayout },
    { path: '/ThremeDetailLayout', name: 'ThremeDetailLayout', component: ThremeDetailLayout },
  ]
})



