<template> 
  <div id="AdvertisingLayout" class="container"> 
        <div >
             <a  :href="link"><img  :src="url" /></a>
        </div>
   </div>
</template>

<script>
export default {
  name: "AdvertisingLayout", 
  props:{
      url:null,
      link:null
  } 
}
</script>

<style scoped>
#AdvertisingLayout {
    margin-bottom: 5px;  
    padding: 5px;
    background-color: white;
} 
img{ 
    height: 100%;
    width: 100%;
} 
</style>



