<template> 
  <div id="TypeLayout">
    <nav class="navbar navbar-default ">
        <div class="container">
            <ul class="nav nav-pills">
                <li   class="nav-item "><a class="nav-link " href="#">全部</a></li>
                <li   class="nav-item"  v-for="item in datas" ><a class="nav-link" href="#">{{item.name}}</a></li> 
            </ul> 
        </div>
    </nav>
  </div>
</template>

<script>
export default {
  name: 'TypeLayout',
  data () {
    return {
      // datas: ['编程技术','谈说','互联网','娱乐']
      datas: []
      }
  },
  mounted: function(){
      this.$http.get('http://localhost:10100/blog/v1/topicType?token=1').then(function(res){
        this.datas = res.data; 
			},function(res){ console.log(res.status) });
  }
}
</script>

<style>
 #TypeLayout{ 
     background-color: white;
 }
 .nav .active{
     background-color: #6fb4fd;
 }
</style>



