<template>
  <div id="HomeLayout">
    <div class="container">
      <div class="row">
        <div class="col">
            <TypeLayout  />
          <div id="carouselExampleIndicators"  class="carousel slide" data-ride="carousel">
            <ol class="carousel-indicators">
              <li data-target="#carouselExampleIndicators" v-for="(item,index) in salidList" :data-slide-to="index" v-bind:class="[index==0 ?'active':'']"  ></li> 
            </ol>
            <div class="carousel-inner" >
              <div class="carousel-item"  v-for="(item,index) in salidList" v-bind:class="[index==0 ?'active':'']"  >
                <img class="d-block w-100" height="410" :src="item.icon" alt="First slide">
              </div>
             
            </div>
            <a class="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
              <span class="carousel-control-prev-icon" aria-hidden="true"></span>
              <span class="sr-only">Previous</span>
            </a>
            <a class="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next">
              <span class="carousel-control-next-icon" aria-hidden="true"></span>
              <span class="sr-only">Next</span>
            </a>
          </div>
           <ThremeListLayout /> 
        </div> 
        <div class="col col-lg-3">
          <HomeNotLogin></HomeNotLogin>
          <AdvertisingLayout v-for="item in advertisList"  :url="item.icon" :link="item.link"></AdvertisingLayout> 
        </div>
      </div>
      <div class="row">
         <FooterLayout/>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HomeLayout',
  components:{ 
      TypeLayout : require('@/components/TypeLayout').default,
      FooterLayout : require('@/components/FooterLayout').default,
      HomeNotLogin : require('@/components/HomeNotLogin').default,
      ThremeListLayout :  require('@/components/ThremeListLayout').default,
      AdvertisingLayout : require('@/components/AdvertisingLayout').default
  },
  data () {
    return { 
      advertisList:[  ],
      salidList:[  ]
    }
  },
  mounted: function(){
     this.load();
  },
  methods:{
    load:function(){
        this.$http.get('http://localhost:10100/blog/v1/advertising/HOME?token=1').then(function(res){
                this.advertisList = res.data; 
              },function(res){ console.log(res.status) });

         this.$http.get('http://localhost:10100/blog/v1/advertising/HOME_SILID?token=1').then(function(res){
                this.salidList = res.data; 
              },function(res){ console.log(res.status) });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style scoped> 
.col{ padding: 5px;}
</style>



