<template>
<div id="ThremeDetailLayout">
  <div class="container">
    <div class="row ">
        <div class="col full-body">  
           <div class="line-30" />
          <div class="title">{{topic.title}}</div>
          <div class="line-30" />
          <div class="row">   </div>
          <div class="row art-body container" v-html="topic.body">    

          </div>
        </div> 
        <div class="col col-lg-3"> 
          <AdvertisingLayout v-for="item in advertisList"  :url="item.icon" :link="item.link"></AdvertisingLayout> 
          <RecommendThremeLayout />
        </div>
    </div>
    <div class="row">
        <FooterLayout/>
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: "ThremeDetailLayout",
  components: {
    HomeNotLogin: require("@/components/HomeNotLogin").default,
    FooterLayout: require("@/components/FooterLayout").default,
    RecommendThremeLayout: require("@/components/RecommendThremeLayout")
      .default,
    AdvertisingLayout: require("@/components/AdvertisingLayout").default
  },
  created: function() {
    this.$data.id = this.$route.query.id;
  },
  data() {
    return {
      advertisList:[],
      topic: {},
      id: ""
    };
  },
  mounted: function() {
    this.load();
  },
  methods:{
    load:function(){
        this.$http .get("http://localhost:10100/blog/v1/topic/" + this.id + "?token=1") .then( function(res) {
          this.topic = res.data;
        },
        function(res) {
          console.log(res.status);
        } );
    
      this.$http.get('http://localhost:10100/blog/v1/advertising/DETAIL?token=1').then(function(res){
                this.advertisList = res.data; 
              },function(res){ console.log(res.status) });


    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style scoped>
#ThremeDetailLayout {
  margin-top: 10px;
}
.full-body {
  background-color: white;
}
.col {
  padding-left: 5px;
}
.title {
  height: 40px;
  line-height: 40px;
  font-size: 20px;
}
.line-30 {
  height: 30px;
}
</style>



